@media only screen and (max-width: 767px) {
  .mobile-nav {
    position: relative;
  }
  
  .menu-btn {
    font-size: 24px;
    padding: 10px;
    background-color: transparent;
    color: #23a6f0;
    border: none;
    cursor: pointer;
    margin-left: -16px;
  }
  
  .nav-menu {
    position: fixed;
    left: -250px;
    top: 57px;
    width: 200px;
    height: 55%;
    background-color: #555;
    color: white;
    transition: left 0.3s ease;
    padding-top: 60px;
    z-index: 99999;
  }
  
  .nav-menu.open {
    left: 0;
  }
  
  .close-btn {
    font-size: 24px;
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    color: white;
    cursor: pointer;
  }
  
  .nav-menu ul {
    list-style-type: none;
    padding: 0;
  }
  
  .nav-menu ul li {
    padding: 10px 20px;
  }
  
  .nav-menu ul li a {
    color: white;
    text-decoration: none;
  }
  
  .nav-menu ul li a:hover {
    background-color: #575757;
    border-radius: 4px;
  }
  
}

